@import "../libs/normalize.css";
html {
  box-sizing: border-box;
}
html ul,
html li,
html h1,
html h2,
html h3,
html h4,
html h5,
html p {
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;
  margin: 0;
  padding: 0;
}
*,
*:after,
*:before {
  box-sizing: inherit;
}
.menu-bar {
  max-width: 1920px;
  width: 100%;
  margin: 0 auto;
  color: #666666;
}
.menu-bar .my-container {
  margin: 0 auto;
  display: flex;
}
.menu-bar .my-container .logo {
  padding: 30px 55px;
  cursor: pointer;
  border-bottom: 4px solid #69d2e700;
}
.menu-bar .my-container .logo:hover {
