}
.menu-bar .my-container .navigation .menu .menu__item {
  padding: 40px 78px;
  border-right: 1px solid #404547;
  border-bottom: 4px solid #69d2e700;
}
.menu-bar .my-container .navigation .menu .menu__item a {
  text-decoration: none;
  color: #666666;
  text-transform: uppercase;
  font-size: 13px;
